import React, { useState, useContext, useEffect } from 'react';
import styles from './Settings.module.css';
import Input from './Input/Input';
import UserContext from '../../../contexts/UserContext';
import { checkEmailValidity } from '../../../utils/validation';
import useUpdateUserInfo from '../../../hooks/useUpdateUserInfo';
import Spinner from '../../../components/Spinner/Spinner';
import setAuthToken from '../../../utils/setAuthToken';

const Settings = ({ setShouldFetch }) => {
  const { userState } = useContext(UserContext);
  const { isUpdatingUserInfo, updateUserInfo, updatedUserInfo } =
    useUpdateUserInfo();
  const [name, setName] = useState({
    value: '',
    invalid: false,
    type: 'text',
    name: 'name',
    placeholder: 'Name',
    errorMessage: 'Please enter your name',
  });
  const [email, setEmail] = useState({
    value: '',
    invalid: false,
    type: 'email',
    name: 'email',
    placeholder: 'Email address',
    errorMessage: 'Please enter your email',
  });

  useEffect(() => {
    if (userState.email.length > 0) {
      setName((prevState) => ({ ...prevState, value: userState.name }));
      setEmail((prevState) => ({ ...prevState, value: userState.email }));
    }
  }, [userState]);

  useEffect(() => {
    if (updatedUserInfo) {
      localStorage.setItem('token', updatedUserInfo.token);
      setAuthToken(updatedUserInfo.token);
      setShouldFetch(true);
    }
  }, [updatedUserInfo, setShouldFetch]);

  const updateClickHandler = (e) => {
    e.preventDefault();
    let isEmailValid = checkEmailValidity(email.value);
    let isNameValid = name.value.trim() !== '';
    if (userState.name === '' && !isNameValid) isNameValid = true;
    let isValid =
      (email.value.trim() !== userState.email ||
        name.value.trim() !== userState.name) &&
      isNameValid &&
      isEmailValid;

    if (isValid && userState.id) {
      updateUserInfo(
        {
          firstName: name.value,
          email: email.value,
        },
        userState.id
      );
    } else {
      if (!isEmailValid) {
        setEmail((prevState) => ({ ...prevState, invalid: true }));
      }
      if (!isNameValid)
        setName((prevState) => ({ ...prevState, invalid: true }));
    }
  };

  const cancelClickHandler = () => {};

  return (
    <div className={styles.Settings}>
      <h1 className={styles.Heading}>Settings</h1>
      <form className={styles.Form} onSubmit={updateClickHandler}>
        <label className={styles.Label} htmlFor='name'>
          Name
        </label>
        <Input {...name} setValue={setName} />
        <label className={styles.Label} htmlFor='email'>
          Email
        </label>
        <Input {...email} setValue={setEmail} />
        <div className={styles.ButtonSpinnerContainer}>
          <button className={styles.UpdateButton} disabled={isUpdatingUserInfo}>
            Update
          </button>
          {isUpdatingUserInfo ? (
            <Spinner style={{ width: '20px', height: '20px' }} />
          ) : null}
        </div>
      </form>
      <div className={styles.Billing}>
        <h1 className={styles.SubHeading}>Billing</h1>
        <p className={styles.Plan}>Current plan: Free</p>
        <p className={styles.Renewal}>Renewal period: Forever</p>
        <button className={styles.CancelButton} onClick={cancelClickHandler}>
          Cancel subscription
        </button>
      </div>
    </div>
  );
};

export default Settings;
