import { fabric } from 'fabric';

const addCachedImage = (imgAsset, imageSrc, canvas, isSvg, id, updateId) => {
  let img = new fabric.CustomImage(imgAsset, {
    originalWidth: imgAsset.width,
    originalHeight: imgAsset.height,
    width: imgAsset.width,
    height: imgAsset.height,
    src: imageSrc,
    scaleX: 256 / imgAsset.width,
    scaleY: 256 / imgAsset.width,
    left: 100,
    top: 100,
    id: id,
    cornerRadius: 0,
    isSvg,
    crossOrigin: 'Anonymous',
  });
  canvas.add(img).setActiveObject(img);
  canvas.requestRenderAll();
  updateId();
};

const loadAndAddImage = (
  imageSrc,
  isSvg,
  canvas,
  isMounted,
  setUserAssets,
  id,
  updateId,
  dispatch
) => {
  let imgEl = new Image();
  imgEl.crossOrigin = 'Anonymous';
  imgEl.src = imageSrc;
  let width = imgEl.width;
  let height = imgEl.height;
  if (width === 0 || height === 0) {
    width = 256;
    height = 320;
  }
  let scale = 256 / width;
  let img = new fabric.CustomImage(imgEl, {
    originalWidth: 0,
    originalHeight: 0,
    width,
    height,
    src: imageSrc,
    scaleX: scale,
    scaleY: scale,
    left: 100,
    top: 100,
    id: id,
    cornerRadius: 0,
    isSvg,
    crossOrigin: 'Anonymous',
    excludeFromExport: true,
  });
  canvas.add(img).setActiveObject(img);
  canvas.requestRenderAll();
  updateId();
  imgEl.onload = () => {
    if (isMounted.current) {
      img.originalWidth = imgEl.width;
      img.originalHeight = imgEl.height;
      img.width = imgEl.width;
      img.height = imgEl.height;
      img.scaleX = 256 / imgEl.width;
      img.scaleY = 256 / imgEl.width;
      img.excludeFromExport = false;
      canvas.requestRenderAll();
      setUserAssets((prevState) => [...prevState, imgEl]);
      dispatch({ type: 'setShouldTriggerCanvasUpdate', data: true });
    }
  };
};

const matchReplacedImage = (active, img, imgAsset) => {
  if (active.cropRect) {
    img.cropRect = {
      x: (active.cropRect.x * imgAsset.width) / active.originalWidth,
      y: (active.cropRect.y * imgAsset.height) / active.originalHeight,
      w: (active.cropRect.w * imgAsset.width) / active.originalWidth,
      h: (active.cropRect.h * imgAsset.height) / active.originalHeight,
    };
    img.width = img.cropRect.w;
    img.height = img.cropRect.h;
  }
  let scale = 1;
  if (img.width * active.scaleX > img.height * active.scaleY) {
    scale = (active.width * active.scaleX) / img.width;
  } else scale = (active.height * active.scaleY) / img.height;
  img.scaleX = scale;
  img.scaleY = scale;
};

const replaceWithCachedImage = (imgAsset, imageSrc, canvas, isSvg, active) => {
  let img = new fabric.CustomImage(imgAsset, {
    originalWidth: imgAsset.width,
    originalHeight: imgAsset.height,
    width: imgAsset.width,
    height: imgAsset.height,
    src: imageSrc,
    top: active.top,
    left: active.left,
    id: active.id,
    cornerRadius: isSvg ? 0 : active.cornerRadius,
    angle: active.angle,
    isSvg,
    crossOrigin: 'Anonymous',
  });
  matchReplacedImage(active, img, imgAsset);
  canvas.remove(active);
  canvas.add(img).setActiveObject(img);
  canvas.requestRenderAll();
};

const loadAndReplaceImage = (
  imageSrc,
  active,
  isSvg,
  canvas,
  isMounted,
  setUserAssets,
  dispatch
) => {
  let imgEl = new Image();
  imgEl.crossOrigin = 'Anonymous';
  imgEl.src = imageSrc;
  let img = new fabric.CustomImage(imgEl, {
    originalWidth: 0,
    originalHeight: 0,
    width: active.width,
    height: active.height,
    src: imageSrc,
    scaleX: active.scaleX,
    scaleY: active.scaleY,
    top: active.top,
    left: active.left,
    id: active.id,
    cornerRadius: isSvg ? 0 : active.cornerRadius,
    angle: active.angle,
    isSvg,
    crossOrigin: 'Anonymous',
    excludeFromExport: true,
    opacity: 0,
  });

  canvas.remove(active);
  canvas.add(img).setActiveObject(img);
  canvas.requestRenderAll();
  imgEl.onload = () => {
    if (isMounted.current) {
      img.originalWidth = imgEl.width;
      img.originalHeight = imgEl.height;
      img.width = imgEl.width;
      img.height = imgEl.height;
      matchReplacedImage(active, img, imgEl);
      img.excludeFromExport = false;
      img.opacity = 1;
      canvas.requestRenderAll();
      setUserAssets((prevState) => [...prevState, imgEl]);
      dispatch({ type: 'setShouldUpdateImage', data: true });
      dispatch({ type: 'setShouldTriggerCanvasUpdate', data: true });
    }
  };
};

const replaceImage = (
  imgAsset,
  imageSrc,
  canvas,
  isSvg,
  isMounted,
  setUserAssets,
  dispatch
) => {
  let active = canvas.getActiveObject();
  if (imgAsset) {
    replaceWithCachedImage(imgAsset, imageSrc, canvas, isSvg, active);
    dispatch({ type: 'setShouldUpdateImage', data: true });
  } else {
    loadAndReplaceImage(
      imageSrc,
      active,
      isSvg,
      canvas,
      isMounted,
      setUserAssets,
      dispatch
    );
  }
  dispatch({ type: 'setShouldReplaceImage', data: false });
};

const addImage = (
  imgAsset,
  imageSrc,
  canvas,
  isSvg,
  isMounted,
  setUserAssets,
  dispatch,
  id,
  updateId
) => {
  if (imgAsset) {
    addCachedImage(imgAsset, imageSrc, canvas, isSvg, id, updateId);
  } else {
    loadAndAddImage(
      imageSrc,
      isSvg,
      canvas,
      isMounted,
      setUserAssets,
      id,
      updateId,
      dispatch
    );
  }
};

export const addNewImage = async (
  state,
  canvas,
  dispatch,
  id,
  updateId,
  userAssets,
  setUserAssets,
  isMounted
) => {
  let imageSrc = state.imageToAdd.src;
  let imgAsset = userAssets.filter((asset) => asset.src === imageSrc)[0];
  let isSvg = state.imageToAdd.type === 'svg';
  if (state.shouldReplaceImage) {
    replaceImage(
      imgAsset,
      imageSrc,
      canvas,
      isSvg,
      isMounted,
      setUserAssets,
      dispatch
    );
  } else {
    addImage(
      imgAsset,
      imageSrc,
      canvas,
      isSvg,
      isMounted,
      setUserAssets,
      dispatch,
      id,
      updateId
    );
  }
  dispatch({ type: 'setImageToAdd', data: null });
};

export const addNewText = (canvas, state, dispatch, id, updateId) => {
  dispatch({ type: 'setShouldAddText', data: false });
  let text = new fabric.CustomTextbox('Addtext', {
    left: 100,
    top: 100,
    width: 130,
    splitByGrapheme: true,
    ...state.textState,
  });
  text.id = id;
  canvas.add(text).setActiveObject(text);
  canvas.requestRenderAll();
  updateId();
};

const newRect = () => {
  let rect = new fabric.Rect({
    left: 100,
    top: 100,
    width: 152,
    height: 152,
    noScaleCache: false,
    strokeUniform: true,
    fill: 'rgba(196,196,196,1)',
    stroke: 'rgba(255,255,255,1)',
    strokeWidth: 0,
    rx: 0,
    ry: 0,
  });
  return rect;
};

const newCircle = () => {
  let circle = new fabric.Circle({
    left: 100,
    top: 100,
    radius: 76,
    noScaleCache: false,
    strokeUniform: true,
    fill: 'rgba(196,196,196,1)',
    stroke: 'rgba(255,255,255,1)',
    strokeWidth: 0,
    rx: 0,
    ry: 0,
  });
  return circle;
};

const newTriangle = () => {
  let triangle = new fabric.Triangle({
    left: 100,
    top: 100,
    width: 152,
    height: 150,
    noScaleCache: false,
    strokeUniform: true,
    fill: 'rgba(196,196,196,1)',
    stroke: 'rgba(255,255,255,1)',
    strokeWidth: 0,
    rx: 0,
    ry: 0,
  });
  return triangle;
};

const newLine = () => {
  let line = new fabric.Line([0, 0, 300, 0], {
    left: 100,
    top: 100,
    stroke: 'rgba(196,196,196,1)',
    strokeWidth: 2,
    noScaleCache: false,
    rx: 0,
    ry: 0,
  });
  return line;
};

export const addNewShape = (state, canvas, dispatch, id, updateId) => {
  dispatch({ type: 'setShapeToAdd', data: null });
  let obj = null;
  if (state.shapeToAdd === 'square') obj = newRect();
  else if (state.shapeToAdd === 'circle') obj = newCircle();
  else if (state.shapeToAdd === 'triangle') obj = newTriangle();
  else if (state.shapeToAdd === 'line') obj = newLine();
  if (obj) {
    obj.id = id;
    canvas.add(obj).setActiveObject(obj);
    canvas.requestRenderAll();
    updateId();
  }
};
