import React, { useEffect, useState, useContext } from 'react';
import styles from './Homepage.module.css';
import SideDrawer from './SideDrawer/SideDrawer';
import Navbar from './Navbar/Navbar';
import Projects from './Projects/Projects';
import Templates from './Templates/Templates';
import Settings from './Settings/Settings';
import { useRouteMatch, Switch, Route, Redirect } from 'react-router-dom';
import useGetVideos from '../../hooks/useGetVideos';
import useGetUserInfo from '../../hooks/useGetUserInfo';
import UserContext from '../../contexts/UserContext';
import useGetTemplates from '../../hooks/useGetTemplates';

const Homepage = () => {
  const { userDispatch } = useContext(UserContext);
  const match = useRouteMatch();
  const { getVideos, videos } = useGetVideos();
  const { getUserInfo, userInfo } = useGetUserInfo();
  const { getTemplates, templates } = useGetTemplates();
  const [shouldFetchVideos, setShouldFetchVideos] = useState(true);
  const [shouldFetchUserInfo, setShouldFetchUserInfo] = useState(true);

  useEffect(() => {
    userDispatch({ type: 'setSelectedTemplate', data: null });
  }, [userDispatch]);

  useEffect(() => {
    if (shouldFetchVideos) {
      setShouldFetchVideos(false);
      getVideos();
    }
  }, [getVideos, shouldFetchVideos]);

  useEffect(() => {
    if (shouldFetchUserInfo) {
      setShouldFetchUserInfo(false);
      getUserInfo();
    }
  }, [shouldFetchUserInfo, getUserInfo]);

  useEffect(() => {
    if (userInfo) {
      let email = userInfo.email;
      let name = '';
      let id = userInfo._id;
      if (userInfo.firstName) name = userInfo.firstName;
      else if (userInfo.googleInfo && userInfo.googleInfo.given_name) {
        name = userInfo.googleInfo.given_name;
      } else if (userInfo.googleInfo && userInfo.googleInfo.name) {
        name = userInfo.googleInfo.name;
      }
      userDispatch({ type: 'setUserInfo', data: { email, name, id } });
    }
  }, [userInfo, userDispatch]);

  return (
    <div className={styles.Homepage}>
      <SideDrawer />
      <div className={styles.Main}>
        <Navbar />
        <Switch>
          <Route path={`${match.path}/templates`}>
            <Templates getTemplates={getTemplates} templates={templates} />
          </Route>
          <Route exact path={`${match.path}/projects`}>
            <Projects videos={videos} setShouldFetch={setShouldFetchVideos} />
          </Route>
          <Route exact path={`${match.path}/settings`}>
            <Settings setShouldFetch={setShouldFetchUserInfo} />
          </Route>
          <Redirect to={`${match.path}/projects`} />
        </Switch>
      </div>
    </div>
  );
};

export default Homepage;
