const deleteVideo = (canvas, obj, setVideos) => {
  let moreThanOne =
    canvas.getObjects().filter((el) => el.type === 'video').length > 1;
  if (moreThanOne) {
    setVideos((prevState) => prevState.filter((video) => video.id !== obj.id));
    canvas.remove(obj);
  }
};

const deleteHandler = (canvas, dispatch, active, setVideos) => {
  if (active.id === 'subtitle') {
    dispatch({ type: 'setIsSubtitles', data: false });
  } else if (!active.isEditing) {
    if (active.type === 'video') deleteVideo(canvas, active, setVideos);
    else if (active.type === 'activeSelection') {
      for (let i = 0; i < active._objects.length; i++) {
        let obj = active._objects[i];
        if (obj.type === 'video') deleteVideo(canvas, obj, setVideos);
        else if (obj.id === 'subtitle') {
          dispatch({ type: 'setIsSubtitles', data: false });
        } else canvas.remove(obj);
      }
    } else canvas.remove(active);
    canvas.discardActiveObject().requestRenderAll();
  }
};

export const keyDownHandler = (
  e,
  canvas,
  setIsPasting,
  setIsCopying,
  setIsCutting,
  setIsBringToFront,
  setIsSendToBack,
  dispatch,
  setIsCut,
  setIsPlayPause,
  setIsPrevWord,
  setIsNextWord,
  setSpeakerVideos
) => {
  let active = canvas && canvas.getActiveObject();
  let canvasInput =
    active && active.type === 'customTextbox' && active.isEditing;
  let isInput = document.activeElement.tagName === 'INPUT' || canvasInput;
  if (!isInput) {
    let ctrl = e.ctrlKey || e.metaKey;
    if (
      e.code === 'Delete' ||
      e.key === 'Delete' ||
      e.code === 'Backspace' ||
      e.key === 'Backspace'
    ) {
      if (active) deleteHandler(canvas, dispatch, active, setSpeakerVideos);
      else setIsCut(true);
    } else if (canvas && (e.code === 'Space' || e.key === 'Space')) {
      e.preventDefault();
      setIsPlayPause(true);
    } else if (canvas && (e.code === 'KeyC' || e.key === 'c') && ctrl) {
      e.preventDefault();
      setIsCopying(true);
    } else if (canvas && (e.code === 'KeyV' || e.key === 'v') && ctrl) {
      e.preventDefault();
      setIsPasting(true);
    } else if (canvas && (e.code === 'KeyX' || e.key === 'x') && ctrl) {
      e.preventDefault();
      setIsCutting(true);
    } else if (canvas && (e.code === 'BracketRight' || e.key === ']') && ctrl) {
      e.preventDefault();
      setIsBringToFront(true);
    } else if (canvas && (e.code === 'BracketLeft' || e.key === '[') && ctrl) {
      e.preventDefault();
      setIsSendToBack(true);
    } else if (e.code === 'ArrowRight' || e.key === 'ArrowRight') {
      e.preventDefault();
      if (active) {
        active.left += 1;
        canvas.fire('object:modified', { target: active });
        canvas.fire('object:moved', { target: active });
      } else setIsNextWord(true);
    } else if (e.code === 'ArrowLeft' || e.key === 'ArrowLeft') {
      e.preventDefault();
      if (active) {
        active.left -= 1;
        canvas.fire('object:modified', { target: active });
        canvas.fire('object:moved', { target: active });
      } else setIsPrevWord(true);
    } else if (e.code === 'ArrowUp' || e.key === 'ArrowUp') {
      if (active) {
        e.preventDefault();
        active.top -= 1;
        canvas.fire('object:modified', { target: active });
        canvas.fire('object:moved', { target: active });
      }
    } else if (e.code === 'ArrowDown' || e.key === 'ArrowDown') {
      if (active) {
        e.preventDefault();
        active.top += 1;
        canvas.fire('object:modified', { target: active });
        canvas.fire('object:moved', { target: active });
      }
    }
  }
};

export const keyUpHandler = (
  setIsPasting,
  setIsCopying,
  setIsCutting,
  setIsBringToFront,
  setIsSendToBack,
  setIsCut,
  setShouldRun,
  setIsPlayPause,
  setIsPrevWord,
  setIsNextWord
) => {
  setIsCopying(false);
  setIsPasting(false);
  setIsCutting(false);
  setIsBringToFront(false);
  setIsSendToBack(false);
  setIsCut(false);
  setIsPlayPause(false);
  setIsPrevWord(false);
  setIsNextWord(false);
  setShouldRun(true);
};
