import { useCallback, useState, useEffect, useRef } from 'react';
import axios from '../axios-instance';

const useGetUserInfo = () => {
  const [isUpdatingUserInfo, setIsUpdatingUserInfo] = useState(false);
  const [updatedUserInfo, setUpdatedUserInfo] = useState(null);
  const isMounted = useRef(false);

  const axiosUpdateUserInfo = useCallback((data, id) => {
    return new Promise((resolve, reject) => {
      axios
        .put(`/user/${id}`, data)
        .then((res) => {
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }, []);

  const updateUserInfo = useCallback(async (data, id) => {
    setUpdatedUserInfo(null);
    setIsUpdatingUserInfo(true);
    try {
      const res = await axiosUpdateUserInfo(data, id);
      if (isMounted.current) setUpdatedUserInfo(res.data);
      console.log(res);
    } catch (err) {
      console.log('Error Updating User Information', err);
    } finally {
      if (isMounted.current) setIsUpdatingUserInfo(false);
      return;
    }
  }, [axiosUpdateUserInfo]);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  return { isUpdatingUserInfo, updateUserInfo, updatedUserInfo };
};

export default useGetUserInfo;