import React from 'react';
import styles from './Webinar.module.css';
import { useHistory } from 'react-router-dom';

const Webinar = ({ templates, dispatch }) => {
  const history = useHistory();
  const templateClickHandler = (template) => {
    dispatch({ type: 'setSelectedTemplate', data: { ...template } });
    history.push('/editor/new');
  };

  return (
    <div className={styles.Webinar}>
      <h2 className={styles.Heading}>Webinar</h2>
      <p className={styles.Subheading}>
        Make your webinar branded and professional looking
      </p>
      <div className={styles.TemplatesList}>
        {templates
          ? templates.commonTemplate.map((template) => (
              <div className={styles.Template} key={template._id}>
                <div
                  className={styles.TemplateInner}
                  onClick={() => templateClickHandler(template)}
                >
                  <div className={styles.TemplateImgContainer}>
                    <img
                      src={template.image_url}
                      className={styles.TemplateImg}
                      alt='template'
                    />
                  </div>
                </div>
              </div>
            ))
          : null}
      </div>
    </div>
  );
};

export default Webinar;
