import React, { useState, useContext } from 'react';
import styles from './PlayerCanvas.module.css';
import EditorContext from '../../../contexts/EditorContext';
import useAddObject from '../hooks/useAddObject';
import useSelectionObserver from '../hooks/useSelectionObserver';
import useGuidelines from '../hooks/useGuidelines';
import useUpdateObject from '../hooks/useUpdateObject';
import useUpdateCanvas from '../hooks/useUpdateCanvas';
import useAddCanvas from '../hooks/useAddCanvas';
import useAddSpeakers from '../hooks/useAddSpeakers';
import useClickListener from '../hooks/useClickListener';
import Toolbar from '../../Toolbar/Toolbar';
import ContextMenu from '../ContextMenu/ContextMenu';
import EventContext from '../../../contexts/EventContext';
import useAnimate from '../hooks/useAnimate';
import CropCanvas from '../CropCanvas/CropCanvas';
import useLoadTemplate from '../hooks/useLoadTemplate';

const PlayerCanvas = ({
  canvas,
  setCanvas,
  video,
  fabricSub,
  setFabricSub,
  currentTime,
  speakers,
  info,
  userAssets,
  setUserAssets,
  templateToLoad,
  setTemplateToLoad,
  clipboard,
  setClipboard,
  objectIdCount,
  setObjectIdCount,
  updatetObjectId,
  speakerVideos,
  setSpeakerVideos,
  canvasRef,
}) => {
  const { editorState, editorDispatch } = useContext(EditorContext);
  const { eventState, eventDispatch } = useContext(EventContext);
  const [isCanvasData, setIsCanvasData] = useState(false);

  useLoadTemplate(
    templateToLoad,
    setTemplateToLoad,
    canvas,
    video,
    editorState,
    editorDispatch,
    setUserAssets,
    setObjectIdCount,
    setSpeakerVideos,
    speakerVideos,
    setFabricSub
  );
  useAnimate(canvas, speakerVideos, video, currentTime);
  useGuidelines(
    true,
    editorState.canvasState,
    canvas,
    eventState,
    eventDispatch,
    editorState.shouldSaveTemplate
  );
  useAddObject(
    editorState,
    editorDispatch,
    canvas,
    objectIdCount,
    updatetObjectId,
    userAssets,
    setUserAssets
  );
  useSelectionObserver(canvas, editorState, editorDispatch, fabricSub);
  useUpdateObject(editorState, editorDispatch, canvas);
  useUpdateCanvas(editorState.canvasState, editorDispatch, canvas);
  useAddCanvas(
    editorState.canvasState,
    setCanvas,
    info,
    setIsCanvasData,
    speakers,
    setObjectIdCount,
    setSpeakerVideos,
    userAssets,
    setFabricSub,
    editorDispatch
  );
  useClickListener(canvas, editorState);
  useAddSpeakers(
    video,
    canvas,
    speakers,
    objectIdCount,
    setObjectIdCount,
    isCanvasData,
    setSpeakerVideos
  );
  
  return (
    <div
      className={styles.CanvasComponent}
      id='canvasComponent'
      ref={canvasRef}
    >
      <div className={styles.Canvas}>
        <canvas id='canvas' />
      </div>
      <CropCanvas
        canvas={canvas}
        userAssets={userAssets}
        canvasRef={canvasRef}
      />
      {editorState.showToolbar &&
      editorState.currentObject.type.length > 0 &&
      editorState.currentObject.type !== 'activeSelection' ? (
        <Toolbar canvas={canvas} type={editorState.currentObject.type} />
      ) : null}
      <ContextMenu
        canvas={canvas}
        objId={objectIdCount}
        updateId={updatetObjectId}
        clipboard={clipboard}
        setClipboard={setClipboard}
        fabricSub={fabricSub}
        dispatch={editorDispatch}
        setSpeakerVideos={setSpeakerVideos}
      />
    </div>
  );
};

export default PlayerCanvas;
