import React, { useContext } from 'react';
import styles from './Navbar.module.css';
import backArrow from '../../assets/editor/backArrow.svg';
import { Link, useHistory } from 'react-router-dom';
import twiso from '../../assets/preview/twiso.svg';
import home from '../../assets/home.svg';
import UserContext from '../../contexts/UserContext';

const Navbar = ({ children, videoData, isPreview, getVideoError, isAuth }) => {
  const { userState } = useContext(UserContext);
  const history = useHistory();
  const backHandler = () => history.goBack();

  const getName = () => {
    let isName = userState.name.length > 0;
    let name = isName ? userState.name : userState.email;
    return isName ? name[0].toUpperCase() + name.slice(1) : name;
  };

  return (
    <header className={styles.Navbar}>
      <div className={styles.BackFileInfoContainer}>
        {!isPreview ? (
          <div className={styles.BackButton} onClick={backHandler}>
            <img className={styles.BackArrow} src={backArrow} alt='back' />
          </div>
        ) : null}

        {isAuth && isPreview ? (
          <div className={styles.HomeLinkContainer}>
            <Link className={styles.HomeLink} to='/home'>
              <img className={styles.Home} src={home} alt='back' />
            </Link>
            <div className={styles.BorderLine}></div>
          </div>
        ) : null}

        {!isAuth && isPreview ? (
          <img className={styles.Twiso} src={twiso} alt='twiso' />
        ) : null}

        {videoData && !isPreview ? (
          <div className={styles.FileInfoContainer}>
            <p className={styles.FileName}>{videoData.title}</p>
            <p className={styles.Creater}>{getName()}</p>
          </div>
        ) : null}

        {isPreview && isAuth && !getVideoError ? (
          <Link
            replace
            to={`/editor/${videoData.id}`}
            className={styles.EditVideo}
          >
            Edit video
          </Link>
        ) : null}
      </div>

      {children}

      {!isAuth && isPreview ? (
        <div className={styles.UserInfoContainer}>
          <Link to={'/auth/sign-in'} className={styles.LogInLink}>
            Log in
          </Link>
          <Link to={'/auth/sign-up'} className={styles.SignUpLink}>
            Get Twiso for free
          </Link>
        </div>
      ) : null}
      
      {isAuth || !isPreview ? (
        <div className={styles.UserInfoContainer}>
          {userState.email.length > 0 ? (
            <div className={styles.UserName}>
              <div className={styles.Initials}>
                <span className={styles.Letter}>{getName()[0]}</span>
              </div>
            </div>
          ) : null}
        </div>
      ) : null}
    </header>
  );
};

export default Navbar;
