import { useCallback, useState, useEffect, useRef } from 'react';
import axios from '../axios-instance';

const useGetUserInfo = () => {
  const [isGettingUserInfo, setIsGettingUserInfo] = useState(false);
  const [userInfo, setUserInfo] = useState(null);
  const isMounted = useRef(false);

  const axiosGetUserInfo = useCallback(() => {
    return new Promise((resolve, reject) => {
      axios
        .get('/user/current')
        .then((res) => {
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }, []);

  const getUserInfo = useCallback(async () => {
    setUserInfo(null);
    setIsGettingUserInfo(true);
    try {
      const res = await axiosGetUserInfo();
      if (isMounted.current) setUserInfo(res.data);
      console.log(res);
    } catch (err) {
      console.log('Error Getting User Information', err);
    } finally {
      if (isMounted.current) setIsGettingUserInfo(false);
      return;
    }
  }, [axiosGetUserInfo]);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  return { isGettingUserInfo, getUserInfo, userInfo };
};

export default useGetUserInfo;