const getBounds = (active, box) => {
  let objectHeight = box.height * box.scaleY;
  let objectWidth = box.width * box.scaleX;
  let boundHeight = active.height * active.scaleY;
  let boundWidth = active.width * active.scaleX;
  let top = box.top;
  let left = box.left;
  let bottom = top + objectHeight;
  let right = left + objectWidth;
  let topBound = active.top;
  let bottomBound = topBound + boundHeight;
  let leftBound = active.left;
  let rightBound = leftBound + boundWidth;
  return {
    top,
    left,
    right,
    bottom,
    leftBound,
    topBound,
    rightBound,
    bottomBound,
    objectWidth,
    objectHeight,
  };
};

export const restrictActive = (active, box) => {
  active.on('moving', (e) => {
    let {
      top,
      left,
      right,
      bottom,
      leftBound,
      topBound,
      rightBound,
      bottomBound,
      objectWidth,
      objectHeight,
    } = getBounds(box, active);
    //left
    if (left > leftBound) active.left = leftBound;
    //top
    if (top > topBound) active.top = topBound;
    //right
    if (right < rightBound) active.left = rightBound - objectWidth;
    //bottom
    if (bottom < bottomBound) active.top = bottomBound - objectHeight;
  });

  active.on('scaling', (e) => {
    let {
      top,
      left,
      right,
      bottom,
      leftBound,
      topBound,
      rightBound,
      bottomBound,
      objectWidth,
      objectHeight,
    } = getBounds(box, active);
    active.left = Math.max(Math.min(left, leftBound), rightBound - objectWidth);
    active.top = Math.max(Math.min(top, topBound), bottomBound - objectHeight);

    if (active.scaleX < box.scaleX || active.scaleY < box.scaleY) {
      active.scaleX = Math.max(box.scaleX, box.scaleY);
      active.scaleY = active.scaleX;
      if (left > leftBound || right < rightBound) active.left = leftBound;
      if (top > topBound || bottom < bottomBound) active.top = topBound;
    }
  });
};

const setHoleBoxPosition = (holeBox, box) => {
  holeBox.left = box.left;
  holeBox.top = box.top;
  holeBox.scaleY = box.scaleY;
  holeBox.scaleX = box.scaleX;
  holeBox.setCoords();
};

export const restrictBox = (active, box, holeBox) => {
  box.on('moving', (e) => {
    let {
      top,
      left,
      right,
      bottom,
      leftBound,
      topBound,
      rightBound,
      bottomBound,
      objectWidth,
      objectHeight,
    } = getBounds(active, box);
    //left
    if (left < leftBound) box.left = leftBound;
    //top
    if (top < topBound) box.top = topBound;
    //right
    if (right > rightBound) box.left = rightBound - objectWidth;
    //bottom
    if (bottom > bottomBound) box.top = bottomBound - objectHeight;

    if (holeBox) setHoleBoxPosition(holeBox, box);
  });

  box.on('scaling', (e) => {
    let {
      top,
      left,
      right,
      bottom,
      leftBound,
      topBound,
      rightBound,
      bottomBound,
      objectWidth,
      objectHeight,
    } = getBounds(active, box);
    box.left = Math.min(Math.max(left, leftBound), rightBound - objectWidth);
    box.top = Math.min(Math.max(top, topBound), bottomBound - objectHeight);
    if (box.scaleX > active.scaleX) {
      box.scaleX = active.scaleX;
      if (left < leftBound || right > rightBound) box.left = leftBound;
    }
    if (box.scaleY > active.scaleY) {
      box.scaleY = active.scaleY;
      if (top < topBound || bottom > bottomBound) box.top = topBound;
    }
    if (holeBox) setHoleBoxPosition(holeBox, box);
  });
};
