import React, { useContext, useState, useEffect, useCallback } from 'react';
import styles from './User.module.css';
import dot from '../../../../assets/homepage/dot.svg';
import UserContext from '../../../../contexts/UserContext';

const User = () => {
  const { userState, userDispatch } = useContext(UserContext);
  const [isDropdown, setIsDropdown] = useState(false);

  const getName = () => {
    let isName = userState.name.length > 0;
    let name = isName ? userState.name : userState.email;
    return isName ? name[0].toUpperCase() + name.slice(1) : name;
  };

  const signOutClickHandler = () => {
    localStorage.removeItem('token');
    userDispatch({ type: 'setIsAuthenticated', data: false });
  };

  const dropdownClickHandler = () => {
    setIsDropdown((prevState) => !prevState);
  };

  const clickHandler = useCallback((e) => {
    for (let i = 0; i < e.path.length; i++) {
      let id = e.path[i].id;
      if (id === 'dropdownContainer' || id === 'signout') return;
    }
    setIsDropdown(false);
  }, []);

  useEffect(() => {
    document.addEventListener('click', clickHandler);
    return () => document.removeEventListener('click', clickHandler);
  }, [clickHandler]);

  return (
    <div className={styles.User}>
      {userState.email.length > 0 ? (
        <div className={styles.UserName}>
          <div className={styles.Initials}>
            <span className={styles.Letter}>{getName()[0]}</span>
          </div>
          <div className={styles.Name}>{getName()}</div>
        </div>
      ) : null}
      <div
        className={styles.DropdownContainer}
        onClick={dropdownClickHandler}
        id='dropdownContainer'
      >
        <div className={styles.Dots}>
          <img src={dot} alt='dot' />
          <img src={dot} alt='dot' />
          <img src={dot} alt='dot' />
        </div>
      </div>
      {isDropdown ? (
        <ul className={styles.Dropdown}>
          <li
            className={styles.SignOut}
            onClick={signOutClickHandler}
            id='signout'
          >
            Sign out
          </li>
        </ul>
      ) : null}
    </div>
  );
};

export default User;
