import { useEffect, useCallback, useRef } from 'react';

const setOffset = (objs, newZoom, newWidth, newHeight, offset) => {
  for (let i = 0; i < objs.length; i++) {
    let obj = objs[i];
    if (obj.id === 'overlay') {
      obj.width = newWidth / newZoom;
      obj.height = newHeight / newZoom;
    } else {
      obj.top -= offset.top;
      obj.left -= offset.left;
    }
  }
};

const getOffset = (obj, prevTop, prevLeft, newZoom, coords) => {
  let offset = { left: 0, top: 0 };
  if (obj) {
    let newTop = prevTop + coords.y / newZoom;
    let newLeft = prevLeft + coords.x / newZoom;
    offset.top = obj.top - newTop;
    offset.left = obj.left - newLeft;
  }
  return offset;
};

const updateSize = (state, cropCanvas) => {
  let windowWidth = window.innerWidth;
  let windowHeight = window.innerHeight;
  let newZoom = Math.min(
    state.width / state.videoWidth,
    state.height / state.videoHeight
  );
  cropCanvas.setZoom(newZoom);
  cropCanvas.setDimensions({
    width: windowWidth,
    height: windowHeight,
  });
  return { newWidth: windowWidth, newHeight: windowHeight, newZoom };
};

const setSize = (state, cropCanvas, canvasRef, coordsRef) => {
  let objs = cropCanvas.getObjects();
  let obj = null;
  let prevTop = null;
  let prevLeft = null;
  if (objs.length > 0) {
    obj = objs.filter((el) => el.id === 'media')[0];
    prevTop = obj.top - coordsRef.current.y / coordsRef.current.zoom;
    prevLeft = obj.left - coordsRef.current.x / coordsRef.current.zoom;
  }
  let { newWidth, newHeight, newZoom } = updateSize(state, cropCanvas);
  if (obj) {
    let coords = canvasRef.current.getBoundingClientRect();
    let offset = getOffset(obj, prevTop, prevLeft, newZoom, coords);
    setOffset(objs, newZoom, newWidth, newHeight, offset);
    coordsRef.current = { x: coords.x, y: coords.y, zoom: newZoom };
  }
  cropCanvas.requestRenderAll();
};

const useUpdateCanvas = (
  cropCanvas,
  canvasState,
  isCropMode,
  cropCanvasRef,
  canvasRef
) => {
  const coordsRef = useRef(null);

  const handleResize = useCallback(() => {
    if (cropCanvas) {
      cropCanvas.resize = canvasState.resize;
      setSize(canvasState, cropCanvas, canvasRef, coordsRef);
    }
  }, [canvasState, cropCanvas, canvasRef]);

  useEffect(() => {
    handleResize();
  }, [handleResize]);

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [handleResize]);

  useEffect(() => {
    if (isCropMode) {
      let coords = canvasRef.current.getBoundingClientRect();
      let zoom = cropCanvas.getZoom();
      coordsRef.current = { x: coords.x, y: coords.y, zoom };
      cropCanvasRef.current.style.display = 'block';
    } else {
      coordsRef.current = null;
      cropCanvasRef.current.style.display = 'none';
    }
  }, [isCropMode, cropCanvas, cropCanvasRef, canvasRef]);
};

export default useUpdateCanvas;
