import { fabric } from 'fabric';
import {
  getOverlayBox,
  getMediaRectCropBox,
  getRectHoleBox,
  getMediaCircleCropBox,
  getCircleHoleBox,
} from './box';

export const reverseMedia = (media) => {
  let cropRect = media.cropRect;
  if (cropRect) {
    media.top = media.top - cropRect.y * media.scaleY;
    media.left = media.left - cropRect.x * media.scaleX;
    media.width = media.originalWidth;
    media.height = media.originalHeight;
    media.cropRect = {
      x: 0,
      y: 0,
      w: media.width,
      h: media.height,
    };
  }
  media.setCoords();
};

const setCropElements = (
  media,
  box,
  holeBox,
  cropCanvas,
  setMedia,
  setCropbox,
  setHoleBox,
  top,
  left
) => {
  cropCanvas.add(holeBox);
  media.top = top;
  media.left = left;
  media.excludeFromExport = true;
  media.id = 'media';
  media.visible = true;
  media.globalCompositeOperation = 'destination-atop';
  media.setControlsVisibility({
    mtr: false,
    ml: false,
    mr: false,
    mt: false,
    mb: false,
  });
  cropCanvas.add(media);
  reverseMedia(media);
  cropCanvas.setActiveObject(media);
  cropCanvas.add(box);
  cropCanvas.requestRenderAll();
  setMedia(media);
  setHoleBox(holeBox);
  setCropbox(box);
};

export const prepareMediaForCrop = (
  active,
  canvas,
  cropCanvas,
  setMedia,
  setCropbox,
  setHoleBox,
  cropType,
  assets,
  canvasRef
) => {
  canvas.discardActiveObject();
  active.visible = false;
  active.angle = 0;
  active.setCoords();
  active.clipPath = null;
  active.strokeWidth = 0;
  let isVideo = active.type === 'video';
  let isRectCrop = cropType === 'rect';
  let box = null;
  let holeBox = null;
  let coords = canvasRef.current.getBoundingClientRect();
  let zoom = cropCanvas.getZoom();
  let top = active.top + coords.y / zoom;
  let left = active.left + coords.x / zoom;
  if (isRectCrop) {
    box = getMediaRectCropBox(active, top, left);
    holeBox = getRectHoleBox(box);
  } else {
    box = getMediaCircleCropBox(active, top, left);
    holeBox = getCircleHoleBox(box);
  }
  let mediaAsset = null;
  if (!isVideo) {
    mediaAsset = assets.filter((asset) => asset.src === active.src)[0];
  } else mediaAsset = active._element;
  cropCanvas.add(getOverlayBox(cropCanvas));

  if (mediaAsset) {
    let ObjectClass = isVideo ? fabric.Video : fabric.CustomImage;
    let media = new ObjectClass(mediaAsset, {
      originalWidth: active.originalWidth,
      originalHeight: active.originalHeight,
      width: active.width,
      height: active.height,
      scaleX: active.scaleX,
      scaleY: active.scaleY,
      cornerRadius: active.cornerRadius,
      cropRect: active.cropRect,
    });
    setCropElements(
      media,
      box,
      holeBox,
      cropCanvas,
      setMedia,
      setCropbox,
      setHoleBox,
      top,
      left
    );
  } else {
    active.clone((cloned) => {
      setCropElements(
        cloned,
        box,
        holeBox,
        cropCanvas,
        setMedia,
        setCropbox,
        setHoleBox,
        top,
        left
      );
    });
  }
};

export const cropMedia = (
  active,
  box,
  media,
  setCropped,
  canvas,
  cropCanvas,
  canvasRef
) => {
  let ratioX = box.scaleX / media.scaleX;
  let ratioY = box.scaleY / media.scaleY;
  let newTop = (box.top - media.top) / media.scaleY;
  let newLeft = (box.left - media.left) / media.scaleX;
  active.width = box.width * ratioX;
  active.height = box.height * ratioY;
  active.scaleX = media.scaleX;
  active.scaleY = media.scaleY;
  active.cropRect = {
    x: newLeft,
    y: newTop,
    w: box.width * ratioX,
    h: box.height * ratioY,
  };
  let coords = canvasRef.current.getBoundingClientRect();
  let zoom = cropCanvas.getZoom();
  active.top = box.top - coords.y / zoom;
  active.left = box.left - coords.x / zoom;
  active.setCoords();

  if (!active.isOnScreen()) {
    let zoom = canvas.getZoom();
    let canvasWidth = canvas.getWidth() / zoom;
    let canvasHeight = canvas.getHeight() / zoom;
    if (active.top > canvasHeight) {
      active.top = canvasHeight - active.height * active.scaleY;
    } else if (active.top < 0) active.top = 0;
    if (active.left > canvasWidth) {
      active.left = canvasWidth - active.width * active.scaleX;
    } else if (active.left < 0) active.left = 0;
  }

  setCropped(active);
};

export const cropFinishHandler = (
  canvas,
  cropCanvas,
  dispatch,
  obj,
  cropType,
  info,
  eventDispatch,
  clearState
) => {
  obj.visible = true;
  obj.angle = info.angle;
  canvas.setActiveObject(obj);
  let type = obj.type;
  obj.setControlsVisibility({
    mtr: type === 'customImage',
    ml: true,
    mr: true,
    mt: true,
    mb: true,
  });
  if (cropType === 'circle') obj.cornerRadius = 100;
  dispatch({ type: 'setCurrentObject', data: { type, object: obj } });
  if (obj.cornerRadius > 0) {
    dispatch({ type: 'setVideoStrokeWidth', data: 0 });
    dispatch({
      type: type === 'video' ? 'setVideoCornerRadius' : 'setImageCornerRadius',
      data: obj.cornerRadius,
    });
  } else if (type === 'video') {
    dispatch({ type: 'setVideoStrokeWidth', data: info.strokeWidth });
  }
  obj.setCoords();
  cropCanvas.clear();
  clearState();
  canvas.requestRenderAll();
  eventDispatch({ type: 'setDbClickEvent', data: null });
  dispatch({ type: 'setShouldCropMedia', data: false });
  dispatch({ type: 'setIsCropMode', data: false });
  dispatch({ type: 'setCurrentCoords', data: obj.lineCoords });
  dispatch({ type: 'setShowToolbar', data: true });
  dispatch({ type: 'setShouldTriggerCanvasUpdate', data: true });
};
