import { useEffect, useCallback, useState } from 'react';
import {
  prepareMediaForCrop,
  cropMedia,
  cropFinishHandler,
} from '../utils/mediaCrop';
import { restrictActive, restrictBox } from '../utils/restrict';

const useCropMedia = (
  state,
  dispatch,
  canvas,
  cropCanvas,
  eventDispatch,
  assets,
  canvasRef
) => {
  const [activeInfo, setActiveInfo] = useState(null);
  const [activeEl, setAcitveEl] = useState(null);
  const [media, setMedia] = useState(null);
  const [holeBox, setHoleBox] = useState(null);
  const [cropbox, setCropbox] = useState(null);
  const [croppedMedia, setCroppedMedia] = useState(null);

  const clearState = useCallback(() => {
    setActiveInfo(null);
    setMedia(null);
    setAcitveEl(null);
    setCropbox(null);
    setCroppedMedia(null);
  }, []);

  // Crop Media
  useEffect(() => {
    if (state.shouldCropMedia && activeEl && cropbox && media) {
      cropMedia(
        activeEl,
        cropbox,
        media,
        setCroppedMedia,
        canvas,
        cropCanvas,
        canvasRef
      );
    } else if (state.shouldCropMedia) setCroppedMedia(activeEl);
  }, [
    state.shouldCropMedia,
    activeEl,
    cropbox,
    media,
    canvas,
    cropCanvas,
    canvasRef,
  ]);

  // If Cropped Media
  useEffect(() => {
    if (croppedMedia && state.shouldCropMedia) {
      cropFinishHandler(
        canvas,
        cropCanvas,
        dispatch,
        croppedMedia,
        state.cropType,
        activeInfo,
        eventDispatch,
        clearState
      );
    }
  }, [
    activeInfo,
    croppedMedia,
    state.shouldCropMedia,
    state.cropType,
    dispatch,
    canvas,
    cropCanvas,
    eventDispatch,
    clearState,
  ]);

  /// Preparation For Crop
  useEffect(() => {
    if (state.isCropMode) {
      let active = canvas.getActiveObject();
      if (active && active.originalWidth > 0 && active.originalHeight > 0) {
        setAcitveEl(active);
        setActiveInfo({
          cornerRadius: active.cornerRadius,
          width: active.width,
          height: active.height,
          top: active.top,
          left: active.left,
          angle: active.angle,
          strokeWidth: active.strokeWidth,
          cropRect: active.cropRect,
        });
        prepareMediaForCrop(
          active,
          canvas,
          cropCanvas,
          setMedia,
          setCropbox,
          setHoleBox,
          state.cropType,
          assets,
          canvasRef
        );
      } else {
        eventDispatch({ type: 'setDbClickEvent', data: null });
        dispatch({ type: 'setIsCropMode', data: false });
      }
    }
  }, [
    state.isCropMode,
    state.cropType,
    canvas,
    cropCanvas,
    dispatch,
    eventDispatch,
    assets,
    canvasRef,
  ]);

  /// If Cropbox and Active El start Crop
  useEffect(() => {
    if (cropbox && media && holeBox) {
      restrictBox(media, cropbox, holeBox);
      restrictActive(media, cropbox);
    }
    return () => {
      if (media) {
        media.off('moving');
        media.off('scaling');
      }
      if (cropbox) {
        cropbox.off('moving');
        cropbox.off('scaling');
      }
    };
  }, [cropbox, media, holeBox]);

  // Listen to Canvas Click
  useEffect(() => {
    if (cropbox && state.isCropMode) {
      cropCanvas.on('mouse:down', (e) => {
        if (
          !e.target ||
          (e.target.type !== 'cropbox' && e.target.id !== 'media')
        ) {
          dispatch({ type: 'setShouldCropMedia', data: true });
          dispatch({ type: 'setShowToolbar', data: false });
        }
      });
      cropCanvas.on('mouse:over', (e) => {
        if (
          e.target &&
          (e.target.type === 'cropbox' || e.target.id === 'media')
        ) {
          cropCanvas.setActiveObject(e.target);
          cropCanvas.renderAll();
        }
      });
      cropCanvas.on('mouse:move', (e) => {
        if (
          e.transform &&
          e.transform.action === 'drag' &&
          e.target.type === 'cropbox'
        ) {
          e.target = media;
          e.transform.target = media;
          e.transform.offsetX = e.absolutePointer.x - media.left;
          e.transform.offsetY = e.absolutePointer.y - media.top;
        }
      });
    }

    return () => {
      if (cropCanvas) {
        cropCanvas.off('mouse:over');
        cropCanvas.off('mouse:down');
        cropCanvas.off('mouse:move');
      }
    };
  }, [cropbox, state.isCropMode, dispatch, cropCanvas, media]);
};

export default useCropMedia;
