const cacheAsset = (src) => {
  return new Promise((resolve, reject) => {
    let img = new Image();
    img.crossOrigin = 'Anonymous';
    img.onload = () => resolve(img);
    img.onerror = () => reject();
    img.src = src;
  });
};

export const cacheAssets = async (arr, setAssets, isMounted) => {
  let filtered = [...new Set(arr)];
  let promises = filtered.map((src) => cacheAsset(src));
  let assets = await Promise.allSettled(promises);
  let assetArr = assets
    .filter((asset) => asset.status === 'fulfilled')
    .map((asset) => asset.value);
  if (isMounted.current) setAssets(assetArr);
};
