export const checkOffScreen = (obj, canvas) => {
  if (!obj.isOnScreen()) {
    let zoom = canvas.getZoom();
    let canvasWidth = canvas.getWidth() / zoom;
    let canvasHeight = canvas.getHeight() / zoom;
    let objHeight = obj.height * obj.scaleY;
    let objWidth = obj.width * obj.scaleX;
    if (obj.id === 'subtitle') {
      obj.left = canvas.getWidth() / canvas.getZoom() / 2;
      obj.top =
        canvas.getHeight() / canvas.getZoom() - obj.height - obj.paddingY;
    } else {
      obj.angle = 0;
      obj.setCoords();
      if (obj.top > canvasHeight) obj.top = canvasHeight - objHeight;
      else if (obj.top < 0) obj.top = 0;
      if (obj.left > canvasWidth) obj.left = canvasWidth - objWidth;
      else if (obj.left < 0) obj.left = 0;
    }
    obj.setCoords();
  }
};

export const checkResizeOffScreen = (object, canvas, dispatch) => {
  let zoom = canvas.getZoom();
  let canvasWidth = canvas.getWidth() / zoom;
  let canvasHeight = canvas.getHeight() / zoom;
  let objHeight = object.height * object.scaleY;
  let objWidth = object.width * object.scaleX;
  let isSub = object.type === 'subtitle';
  let isTextObject = object.type === 'customTextbox' || isSub;
  let isVertical = canvas.resize === 'vertical';
  let shouldResize =
    isVertical &&
    (objWidth > (canvasWidth * 2) / 3 || objHeight > (canvasHeight * 2) / 3);

  if (shouldResize && isTextObject) {
    object.width = canvasWidth / 2;
    objWidth = canvasWidth / 2;
  } else if (shouldResize) {
    let desiredSize = (canvasWidth * 2) / 3;
    let scale = desiredSize / object.width;
    object.scaleX = scale;
    object.scaleY = scale;
    objWidth = object.width * object.scaleX;
    objHeight = object.height * object.scaleY;
  }

  if (isTextObject && isVertical && +object.fontSize > 30 && dispatch) {
    object.fontSize = 30;
    dispatch({
      type: isSub ? 'setSubtitlesFontSize' : 'setFontSize',
      data: 30,
    });
  }
  if (!object.isOnScreen() && !isSub) {
    object.angle = 0;
    object.setCoords();
    if (object.top > canvasHeight) object.top = canvasHeight - objHeight;
    else if (object.top < 0) object.top = 0;
    if (object.left > canvasWidth) object.left = canvasWidth - objWidth;
    else if (object.left < 0) object.left = 0;
  } else if (isSub) {
    object.left = canvasWidth / 2;
    let top = canvasHeight - object.height - object.paddingY;
    object.top = top > 0 ? top : object.height / 2;
    object.width = 550 < canvasWidth / 2 ? 550 : canvasWidth / 2;
  }
  object.setCoords();
};
