import { fabric } from 'fabric';
import { checkOffScreen } from './checkOffScreen';
const pasteObject = (obj, id, top, left) => {
  return new Promise((resolve, reject) => {
    obj.clone((cloned) => {
      cloned.id = id;
      cloned.top = top;
      cloned.left = left;
      resolve(cloned);
    });
  });
};

export const handlePaste = async (
  canvas,
  clipboard,
  objId,
  updateId,
  setClipboard,
  dispatch,
  fabricSub,
  setSpeakerVideos
) => {
  canvas.discardActiveObject();
  let top = clipboard.coords.top + 15;
  let left = clipboard.coords.left + 15;
  let type = clipboard.object.type;
  let videosArr = [];
  if (type === 'activeSelection') {
    let objArr = [];
    for (let i = 0; i < clipboard.object._objects.length; i++) {
      let obj = clipboard.object._objects[i];
      if (obj.type !== 'subtitle') {
        let cloned = await pasteObject(
          obj,
          objId + i,
          obj.top + top - clipboard.object.top,
          obj.left + left - clipboard.object.left
        );
        objArr.push(cloned);
        updateId();
      }
    }
    for (let i = 0; i < objArr.length; i++) {
      let obj = objArr[i];
      if (obj.type === 'video') videosArr.push(obj);
      canvas.add(obj);
      canvas.renderAll();
      checkOffScreen(obj, canvas);
    }
    let selection = new fabric.ActiveSelection(objArr, { canvas });
    canvas.setActiveObject(selection);
  } else {
    let cloned = await pasteObject(clipboard.object, objId, top, left);
    if (type === 'video') videosArr.push(cloned);
    updateId();
    canvas.add(cloned).setActiveObject(cloned);
    checkOffScreen(cloned, canvas);
  }
  if (videosArr.length > 0) {
    setSpeakerVideos((prevState) => [...prevState, ...videosArr]);
  }
  if (fabricSub) canvas.bringToFront(fabricSub);
  canvas.requestRenderAll();
  if (dispatch) dispatch({ type: 'setRightClickEvent', data: null });
  setClipboard({
    object: clipboard.object,
    coords: { top, left },
  });
};
