import React, { useEffect, useContext } from 'react';
import styles from './Uploader.module.css';
import DragDrop from './DragDrop/DragDrop';
import Progress from '../../components/Progress/Progress';
import useUploadVideo from '../../hooks/useUploadVideo';
import Navbar from '../../components/Navbar/Navbar';
import UserContext from '../../contexts/UserContext';
import { useHistory } from 'react-router-dom';

const Uploader = ({ viedoForUpload, setVideoForUpload, setVideoData }) => {
  const { userState } = useContext(UserContext);
  const { uploadVideo, info, uploadProgress } = useUploadVideo();
  const history = useHistory();
  
  useEffect(() => {
    if (info && userState.selectedTemplate) {
      history.replace(`/editor/${info.id}`);
    } else if (info) setVideoData(info);
  }, [history, info, setVideoData, userState.selectedTemplate]);

  useEffect(() => {
    if (viedoForUpload) {
      const formData = new FormData();
      formData.append('video', viedoForUpload);
      uploadVideo(formData);
    }
  }, [viedoForUpload, uploadVideo]);

  return (
    <>
      <Navbar />
      <div className={styles.Uploader}>
        {!viedoForUpload ? (
          <DragDrop setVideoForUpload={setVideoForUpload} />
        ) : (
          <Progress progress={uploadProgress} heading='Uploading your video' />
        )}
      </div>
    </>
  );
};

export default Uploader;
